import React, { Component } from "react";
import "./education.css";
export default class Education extends Component {
  render() {
    return (
      <div className="content" id="education">
        <section className="colorlib-experience" data-section="education">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div
                className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box"
                data-animate-effect="fadeInLeft"
              >
                <span className="heading-meta"></span>
                <h2 className="colorlib-heading animate-box">Education</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                  <article
                    className="timeline-entry animate-box"
                    data-animate-effect="fadeInTop"
                  >
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-66">
                        <i className="icon-book" />
                      </div>
                      <div className="timeline-label">
                        <h2>
                          Web Developement Degree<span> 2019</span>
                        </h2>
                        <p> Highline Community College - Des Moines, WA </p>
                        <p>GPA: 3.8/4.0</p>
                      </div>
                    </div>
                  </article>
                  <article
                    className="timeline-entry animate-box"
                    data-animate-effect="fadeInTop"
                  >
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-77">
                        <i className="icon-book" />
                      </div>
                      <div className="timeline-label">
                        <h2>
                          Web Designer Certification <span> 2019</span>
                        </h2>
                        <p> Highline Community College - Des Moines, WA </p>
                        <p> GPA: 3.9/4.0 </p>
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
